import React from "react"

import { Container, Icons, StyledLink } from "../styles/styledHome"
import { FaLinkedin, FaGithubSquare} from "react-icons/fa"
import ProfilePic from "../assets/foto.png"
import Resume from "../assets/resume.pdf"

const Home = () => {

    return(
        <Container>
            <div>
                <div id="profile">
                    <img src={ProfilePic}/>
                    <a href="/" >About</a>
                    <a href="/education" >Education</a>
                    <a href="/" >Skills</a>
                    <a href="/" >Publications</a>

                </div>

                <div id="profile_content">
                    <h1>Gustavo Charamba</h1>
                    <p>Web Developer - Full-Stack</p>
                    <a href={Resume} download>Download Resume</a>
                    <div>
                        <a href="https://www.linkedin.com/in/gustavocharamba/"><FaLinkedin style={Icons}/></a>
                        <a href="https://github.com/advaner"><FaGithubSquare style={Icons}/></a>
                    </div>
                </div>
            </div>
        </Container>
    )
}

export default Home