import styled from "styled-components"
import colors from "./Colors"

export const Container = styled.div`
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Noto Sans JP', sans-serif;
    color: ${colors.blackGray};
    background-color: ${colors.lightGray};
    margin: 0;

    & > div {
        height: 100%;
        width: 70%;
        display: flex;

        & > #profile {
            width: 25%;
            height: 100%;
            background-color: ${colors.blackGray};
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            & > img {
                width: 125px;
                height: 125px;
                border-radius: 50%;
                margin-bottom: 50px;
            }

            & > a {
                width: 20%;
                height: 5%;
                color: ${colors.lightGray};
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                transition: 200ms;
                text-decoration: none;

                :hover {
                    filter: opacity(75%);
                }
            }
        }

        & > #profile_content {
            width: 75%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            & > h1 {
                font-size: 54px;
                font-weight: 700;
            }

            & > p {
                font-size: 20px;
                font-weight: 400;
            }

            & > a {
                color: ${colors.darkGray};
                text-decoration: none;
                cursor: pointer;
                transition: 200ms;
                :hover {
                    filter: opacity(75%);
                }
            }

            & > div {
                width: 12%;
                display: flex;
                justify-content: space-between;

                & > a {
                    height: 50px;
                    width: 50px;
                    cursor: pointer;
                    transition: 200ms;
                    :visited{
                       text-decoration:overline ;
                    }
                    :hover {
                        filter: opacity(75%);
                    }
                }
            }
        }
    }
`

export const Icons = {
    marginTop: "30px",
    height: "50px",
    width: "50px",
    color: `${colors.blackGray}`
}
